import { memo } from "react"
import { Link } from "react-router-dom"

const ButtonAdd = ({ text, to }: ButtonsLink) => {
    return (
        <div className="row">
            <div className="d-flex">
                <Link to={to} className="btn btn-primary">
                    {text}
                </Link>
            </div>
        </div>
    )
}

export default memo(ButtonAdd)
