import { memo, useContext } from "react"
import { SearchContext } from "../Pages/Components/Table"
import { get } from "../Functions/Api"

const Search = () => {
    const { url, dispatch } = useContext(SearchContext)

    const handleSearch = async (value: string) => {
        if (!url || !dispatch) return

        const urlParse = getUrl(url, value)
        const res = await get(urlParse)
        if (res.ok) dispatch(res.data)
    }

    const search = async (evt: React.FormEvent<HTMLFormElement>) => {
        evt.preventDefault()

        const inputElemnet = evt.currentTarget.querySelector(
            `[name="search"]`
        ) as HTMLInputElement
        handleSearch(inputElemnet.value)
    }

    const getUrl = (url: string, keyword: string) => {
        return url ? url.replace(":keyword", keyword) : ""
    }

    const getAll: React.KeyboardEventHandler<HTMLInputElement> = async (
        evt
    ) => {
        const value = evt.currentTarget.value
        if (value) return
        handleSearch(value)
    }

    return (
        <div className="search">
            <form className="container-fluid" onSubmit={search}>
                <div className="input-group">
                    <input
                        type="text"
                        className="form-control"
                        name="search"
                        placeholder="Cari berdasarkan NIK"
                        aria-label="NIK"
                        aria-describedby="basic-addon1"
                        onKeyUp={getAll}
                    />
                    <span className="input-group-text" id="basic-addon1">
                        <i className="bi bi-search"></i>
                    </span>
                </div>
            </form>
        </div>
    )
}

export default memo(Search)
