import PageTitle from "./Components/PageTitle"
import Table from "./Components/Table"
import { useCallback, useEffect, useState } from "react"
import ButtonAdd from "./Components/Buttons/ButtonAdd"
import { Link, useParams } from "react-router-dom"
import { get } from "../Functions/Api"
import { useAuth } from "../Hooks/useAuth"
import { PopupAlert } from "../Functions/Alerts"

const AjukanSurat = () => {
    const [ajukanSurat, setAjukanSurat] = useState<DataT[]>()
    const { type } = useParams()
    const { token_access } = useAuth()

    const getAjukanSurat = useCallback(async () => {
        const res = await get("surat")
        if (res.ok) setAjukanSurat(res.data)
    }, [])

    const download: React.MouseEventHandler<HTMLButtonElement> = async (
        evt
    ) => {
        const target = evt.currentTarget
        const { file_surat } = target.dataset

        fetch(
            `surat/file/download?file_name=${file_surat}&token=${token_access}`,
            {
                headers: {
                    Authorization: `Bearer ${token_access}`,
                    "Content-Type": "application/pdf"
                }
            }
        )
            .then((res) => res.blob())
            .then((blob) => {
                const linkDownload = document.createElement("a")
                linkDownload.href = URL.createObjectURL(blob)
                linkDownload.target = "_blank"
                linkDownload.download = "surat.pdf"
                linkDownload.click()
            })
            .catch(() => {
                PopupAlert.fire({
                    icon: "error",
                    text: "Gagal mendownload surat!"
                })
            })
    }

    useEffect(() => {
        getAjukanSurat()
    }, [getAjukanSurat])

    const RowHead = (
        <tr>
            <th>No</th>
            <th>NIK</th>
            <th>Jenis Surat</th>
            <th>Status</th>
            <th colSpan={2}>ACTION</th>
        </tr>
    )

    return (
        <>
            <PageTitle title="Ajukan Surat" />
            <ButtonAdd text="Ajukan Surat" to={`/${type}/ajukan-surat/add`} />
            <Table
                title="Tabel Pengajuan Surat"
                rowThead={RowHead}
                search={{
                    url: "surat?search=:keyword",
                    dispatch: setAjukanSurat
                }}
            >
                {ajukanSurat &&
                    ajukanSurat.map(
                        (
                            {
                                pemohon: nik,
                                info: { nama },
                                id,
                                acc,
                                file_surat
                            },
                            i
                        ) => {
                            return (
                                <tr key={i}>
                                    <td>{++i}</td>
                                    <td>{nik}</td>
                                    <td>{nama}</td>
                                    <td>
                                        {!acc ? (
                                            <button
                                                className="btn btn-warning d-flex align-items-center gap-1 mx-auto"
                                                disabled
                                            >
                                                <i className="bi bi-arrow-clockwise" />
                                                Proses
                                            </button>
                                        ) : (
                                            <button
                                                className="btn btn-success d-flex align-items-center gap-1 mx-auto"
                                                disabled
                                            >
                                                <i className="bi bi-check-circle" />
                                                Selesai
                                            </button>
                                        )}
                                    </td>
                                    <td className="d-flex justify-content-center align-items-center mt-3 gap-3">
                                        <Link
                                            to={`cetak/${id}?nama_file=${file_surat}`}
                                            className={`text-${
                                                acc ? "primary" : "secondary"
                                            } `}
                                            target="_blank"
                                            rel="noreferrer"
                                            style={
                                                !acc
                                                    ? {
                                                          pointerEvents: "none",
                                                          color: "gray",
                                                          textDecoration: "none"
                                                      }
                                                    : {}
                                            }
                                        >
                                            <i
                                                className="bi bi-printer"
                                                data-feather="detail-data"
                                            />
                                        </Link>
                                        <button
                                            className={`btn text-${
                                                acc ? "success" : "secondary"
                                            } `}
                                            data-file_surat={file_surat}
                                            onClick={download}
                                        >
                                            <i
                                                className="bi bi-download"
                                                data-feather="detail-data"
                                            />
                                        </button>
                                    </td>
                                </tr>
                            )
                        }
                    )}
            </Table>
        </>
    )
}

export default AjukanSurat
